import React from 'react'

import { Link } from 'react-router-dom'

import Col from 'react-bootstrap/Col'
import Card from 'react-bootstrap/Card'


const ProjectCard = props => {

    return (
        <Col sm={12}>
            <Card bg="light" border="light" as="article">
                <Card.Img variant="top" src={props.poster} />
                <Card.Body>
                    <Card.Title>{props.title}</Card.Title>
                    <Card.Title>{props.genre} - {props.format}</Card.Title>
                    <Card.Text> {props.description} </Card.Text>
                    {/* <Card.Text> Lugar: {props.location} </Card.Text> */}
                    <Link to={`/projects/details/${props._id}`} className="btn btn-dark btn-block btn-sm">Visitar Proyecto</Link>
                </Card.Body>
            </Card>
        </Col>
    )
}

export default ProjectCard