import React, { Component } from 'react'
import Projects from '../../../service/projects.service'
import ProjectCard from './ProjectCard'
import NewProject from './new/NewProject'

import Container from 'react-bootstrap/Container'
import Button from 'react-bootstrap/Button'
import Row from 'react-bootstrap/Row'

import Toast from 'react-bootstrap/Toast'
import Modal from 'react-bootstrap/Modal'


class ProjectsList extends Component {

    constructor() {
        super()
        this.state = {
            modalShow: false,
            // toast: {
            //     show: false,
            //     text: ''
            // },
            projects: [],
        }
        this.projectsService = new Projects()
    }

    handleModal = visible => this.setState({ modalShow: visible })
    // handletoast = (visible, text = '') => {
    //     const toastCopy = { ...this.state.toast }
    //     toastCopy.show = visible
    //     toastCopy.text = text
    //     this.setState({ toast: toastCopy })
    // }

    getAllProjects = () => {
        this.projectsService.getProjects()
            .then(response => this.setState({ projects: response.data }))
            .catch(err => console.log(err))
    }

    componentDidMount = () => {
        this.getAllProjects()
    }

    finishNewProject = () => {
        this.getAllProjects()
        this.handleModal(false)
       // this.handletoast(true, 'Registro creado en BBDD')
    }

    render() {
        return (
            <Container as="section">
                <h1>Listado de proyectos</h1>
                <Button onClick={this.handleModal} variant="dark" size="lg">Crear un nuevo proyecto</Button>
                <Row>
                    {this.state.projects.map(elm => <ProjectCard key={elm._id} {...elm} />)}
                </Row>

                <Modal show={this.state.modalShow} onHide={() => this.handleModal(false)}>
                    <Modal.Body>
                        <NewProject finishNewProject={this.finishNewProject} closeModal={() => this.handleModal(false)}/>
                    </Modal.Body>
                </Modal>
            </Container>
        )

    }

}

export default ProjectsList