    return (
        <Col ld={3} md={4} sm={6}>
            <Card bg="light" border="light" as="article">
        <Link to={`/filmmakers/${props._id}`} className="btn btn-block">
                <Card.Img variant="top" src={props.picture} />
                <Card.Body>
                    <Card.Title>{props.name} {props.lastname}</Card.Title>
                    <Card.Text> Equipo de {props.team} - {props.age} años</Card.Text>
                 {/* <Link to={`/filmmakers/${props._id}`} className="btn btn-dark btn-block btn-sm">Visitar Perfil</Link> */}
                </Card.Body>
        </Link>
            </Card>
        </Col>
    )
